@use '../../reference' as reference;

.header {
    background-color: reference.$black-special;
    display: grid;
    align-items: center;
    color: reference.$white;
    position: absolute;
    top: 0px;
    z-index: 1500;

    @media (min-width: 768px) {
        width: 100%;
        height: 80px;
    }

    @media (max-width: 768px) {
        width: 100%;
        height: 40px;
    }

    &Content {
        font-family: Audi-Light;
        color: reference.$white;

        a {
            text-decoration: none;
            color: reference.$white;
            transition: color reference.$transition-long;
        }

        a:hover {
            color: reference.$light-blue;
            transition: color reference.$transition-long;
            cursor: pointer;
        }

        @media (min-width: 768px) {
            margin: 0 auto;
            width: 960px;
        }

        @media (max-width: 768px) {
            margin: 0 auto;
            width: 340px;
        }
    }

    &Image {
        @media (min-width: 768px) {
            height: 32px;
        }

        @media (max-width: 768px) {
            height: 24px;
        }
    }

    &Logo {
        font-family: Audi-Light;
        font-size: 24px;
    }
}

.subheader {
    background-color: reference.$black-special;
    display: grid;
    align-items: center;
    position: absolute;
    z-index: 1500;

    @media (min-width: 768px) {
        width: 100%;
        // height: 150px;
        top: 80px;
    }

    @media (max-width: 768px) {
        width: 100%;
        // height: 150px;
        top: 40px;
    }

    &Content {
        font-family: Audi-Light;
        color: reference.$white;

        a {
            text-decoration: none;
            color: reference.$white;
            transition: color reference.$transition-long;
        }

        a:hover {
            color: reference.$light-blue;
            transition: color reference.$transition-long;
            cursor: pointer;
        }

        @media (min-width: 768px) {
            margin: 0 auto;
            width: 960px;
        }

        @media (max-width: 768px) {
            margin: 0 auto;
            width: 340px;
        }
    }
}
