.smallCaps {
    font-variant: small-caps;
}

.textDecoration {
    &None {
        text-decoration-line: none;
    }

    &Overline {
        text-decoration-line: overline;
    }

    &Underline {
        text-decoration-line: underline;
    }

    &LineThrough {
        text-decoration-line: line-through;
    }
}

.align {
    &Inherit {
        text-align: inherit;
    }

    &Left {
        text-align: left;
    }

    &Center {
        text-align: center;
    }

    &Right {
        text-align: right;
    }

    &Justify {
        text-align: justify;
    }
}

.inline {
    display: inline;
}

.color {
    &Black {
        color: reference.$black;
    }

    &DarkerGrey {
        color: reference.$darker-grey;
    }

    &DarkGrey {
        color: reference.$dark-grey;
    }

    &MediumGrey {
        color: reference.$medium-grey;
    }

    &LightGrey {
        color: reference.$light-grey;
    }

    &LighterGrey {
        color: reference.$lighter-grey;
    }

    &LightBlue {
        color: reference.$light-blue;
    }

    &White {
        color: reference.$white;
    }

    &Transparent {
        color: reference.$transparent;
    }
}

.paragraphMargins {
    @media (min-width: 768px) {
        margin-bottom: 30px;
    }

    @media (max-width: 768px) {
        margin-bottom: 15px;
    }
}

.shadow {
    text-shadow: 0px 0px 5px reference.$darker-grey;
}

a {
    // This isn't very versatile but I suppose links will always just be white
    color: reference.$white;
    text-decoration: none;
    transition: color reference.$transition-long;

    &:hover {
        color: reference.$light-blue;
        transition: color reference.$transition-long;
        cursor: pointer;
    }
}
